import React from "react";
import "../App.css";
import sha512 from "js-sha512";
import axios from "axios";
import { backend_base_url, client_base_url, TEST_URL } from "../config/urls";
import { marchant_key, marchant_salt_v1 } from "../config/confidential";

function Home() {
  const transactionHandler = async () => {
    console.log("transaction Started");

    // mandatory fields
    /* 

      1. hash = sha512(key|txnid|amount|productinfo|firstname|email|||||||||||SALT)
      
      2. key :  marchant key : generated at payU account 

      3. txnid : transaction id : generated by the marchant : limit 25 varchar

      4. amount : Product Amount  : must be float

      5. productinfo : Product desctiption : limt 100 varchar

      6. firstname : customer's first name

      7. lastname : customer's last name

      8. email : customer's email address

      9. phone : customer's mobile number

      10. surl : success URL : user will be redirected to this url if the transaction is successful

      11. furl : failure URL : user will be redirected to this url if the transcation fails

      12. curl : ( optional ) : cancel URL : user will be redirected to this url if he/she cancel the transaction in mid
    */

    // const hash=sha512(key|txnid|amount|productinfo|firstname|email|||||||||||SALT)

    // Requesting backend to generate a new Entry for new Transaction

    var txnId;

    await axios({
      method: "GET",
      url: backend_base_url + "txn/",
    })
      .then((res) => {
        txnId = res.data.txnID;
      })
      .catch((err) => {
        console.log(err);
      });

    console.log(txnId, "transactions");

    const key = marchant_key;
    const amount = "10000.00";
    const productinfo = "tshirt500";
    const firstname = "harish";
    const lastname = "jangir";
    const email = "harishjangid0838@gmail.com";
    const phone = "9079440677";
    const surl = client_base_url + "success";
    const furl = client_base_url + "failed";
    const curl = client_base_url + "canceled";
    const salt = marchant_salt_v1;
    const feed = key + txnId + amount + productinfo + firstname + email + salt;
    console.log(feed);
    const hash = sha512(feed);

    console.log(hash);
    const data = {
      key,
      hash,
      txnId,
      amount,
      productinfo,
      firstname,
      lastname,
      email,
      phone,
      surl,
      furl,
      curl,
    };

    console.log(data);
    axios({
      url: TEST_URL,
      method: "POST",
      data: data,
      headers: {
        "Access-Control-Allow-Origin": "http://localhost:3000",
        "Content-Type": "application/json",
        mode: "no-cors",
      },
    })
      .then((res) => {
        console.log(res);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <div className="App">
      <button onClick={transactionHandler}> Click to Payment</button>
    </div>
  );
}

export default Home;
